/* CSS Reset using Universal Selector */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Light.ttf") format("ttf");
}

.div-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  background-color: #b2ac88;
}

.centered-large-text {
  margin: auto;
  text-align: center;

  font-size: 64px;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
}

.animated {
  animation-duration: 2.5s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
}

.bounce {
  animation-name: bounce;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(-10);
  }
  30% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(25px);
  }
  80% {
    transform: translateY(-10px);
  }
}
